<script>
export default {
  methods:{
    exit(){     
      localStorage.clear()
      this.$router.push("/");
    }
  },
  created(){    
    if (!localStorage.getItem('token')) {
      this.$router.push("/");
    }else{
      this.$router.push("/home");
    }
  }
}
</script>

<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

